<template>
  <div>
    <van-nav-bar title="编辑地址" left-text="返回" left-arrow @click-left="$router.go(-1)" />
    <van-address-edit
      :area-list="areaList"
      show-postal
      show-set-default
      show-search-result
      :search-result="searchResult"
      :area-columns-placeholder="['请选择', '请选择', '请选择']"
      @save="onSave"
    />
  </div>
</template>

<script>
import areaList from "./erea.js";
export default {
  data() {
    return {
      areaList,
      searchResult: []
    };
  },
  methods: {
    onSave(con) {
      if (con.isDefault) {
        this.$store.state.address.list.forEach(item => {
            item.isDefault = false
        });
      }
      con.address = con.province+con.city+con.county+con.addressDetail
      con.id = this.$store.state.address.list.length+1
      this.$store.state.address.list.push(con)
      this.$router.go(-1)
      // console.log(this.$store.state.address.list);
    }
  },
  created() {
    this.$store.state.bool = false;
  },
  // 生命周期钩子，最后销毁函数
  destroyed() {
    // 当组件被销毁时候，修改导航数据
    // this.$store.state.bool = true;
  }
};
</script>

